.bounce {
  position:fixed;
  left:50%;
  bottom:0;
  margin-top:-25px;
  margin-left:-25px;
  height:50px;
  width:50px;
  background:red;
  animation:bounce 1s infinite;
}

@-webkit-keyframes bounce {
  0%       { bottom:5px; }
  25%, 75% { bottom:15px; }
  50%      { bottom:20px; }
  100%     {bottom:0;}
}

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}
ol, ul {
  list-style: none;
}
blockquote, q {
  quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}

/*-----------------variables-----------------*/
@import url('https://fonts.googleapis.com/css?family=Roboto:300,400,700');
$main-font: 'Roboto', sans-serif;
$green: #57de9c;
$yellow: rgb(255, 237, 99);
$black: #000;
$white: #fff;

/*-----------------html5 blocks--------------*/

* {
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
}

body {
  font: 1.6rem $main-font;
  color: $black;
  overflow-x: hidden;
  line-height: 1.5;
}

img {
  max-width: 100%;
}

a {
  color: inherit;
  text-decoration: none;
}

p {
  margin-bottom: 25px;
}

/*----------------basic styles-----------------*/
.section {
  padding: 9rem 10px;
}

.list {
  dd {
    display: list-item;
    margin-left: 20px;
    list-style: disc outside;
  }

  .sm-title {
    margin-top: 3rem;
  }
}

.align-c {
  text-align: center;
}

.link {
  color: $green;
  transition: .3s all;

  &:hover, &:focus {
    text-decoration: underline;
  }
}

.section-title {
  color: $green;
  font-size: 6.5rem;
  font-weight: normal;
  text-align: center;
  margin-bottom: 7rem;
}

.md-title {
  font-size: 3rem;
  font-weight: 300;
}

.sm-title {
  font-size: 2.4rem;
  font-weight: bold;
  margin-bottom: 2rem;
}

.green-section {
  color: $white;
  background-color: $green;

  .section-title {
    color: $white;
  }
}

.yellow-section{
  background-color: $yellow;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
}

.flex {
  display: flex;
  justify-content: space-between;
  flex-flow: wrap row;
}